<template>
	<main class="content container">
		<div class="content__top">
			<ul class="breadcrumbs">
				<li class="breadcrumbs__item">
					<a class="breadcrumbs__link" href="/">
						Каталог
					</a>
				</li>
				<li class="breadcrumbs__item">
					<a class="breadcrumbs__link" href="/#/cart">
						Корзина
					</a>
				</li>
				<li class="breadcrumbs__item">
					<a class="breadcrumbs__link">
						Оформление заказа
					</a>
				</li>
			</ul>

			<h1 class="content__title" v-show="$store.state.orderInfoLoading">
				Заказ загружаем
			</h1>
			<h1 class="content__title" v-show="!$store.state.orderInfoLoading && !this.$store.state.orderNotFound">
				Заказ <span v-if="orderInfo != null">{{ orderInfo.id }}</span> оформлен 
			</h1>
			<h1 class="content__title" v-show="this.$store.state.orderNotFound">
				Заказ <span v-if="orderInfo != null">{{ orderInfo.id }}</span> не найден 
			</h1>
		</div>
		<section class="cart" v-show="!$store.state.orderInfoLoading">
			<form class="cart__form form" action="#" method="POST" v-if="orderInfo != null">
				<div class="cart__field">
					<p class="cart__message">
						Благодарим за выбор нашего магазина. На Вашу почту придет письмо с деталями заказа. 
						Наши менеджеры свяжутся с Вами в течение часа для уточнения деталей доставки.
					</p>

					<ul class="dictionary">
						<li class="dictionary__item">
							<span class="dictionary__key">
								Получатель
							</span>
							<span class="dictionary__value">
								{{ orderInfo.name }}
							</span>
						</li>
						<li class="dictionary__item">
							<span class="dictionary__key">
								Адрес доставки
							</span>
							<span class="dictionary__value">
								{{ orderInfo.address }}
							</span>
						</li>
						<li class="dictionary__item">
							<span class="dictionary__key">
								Телефон
							</span>
							<span class="dictionary__value">
								{{ orderInfo.phone }}
							</span>
						</li>
						<li class="dictionary__item">
							<span class="dictionary__key">
								Email
							</span>
							<span class="dictionary__value">
								{{ orderInfo.email }}
							</span>
						</li>
						<li class="dictionary__item">
							<span class="dictionary__key">
								Способ оплаты
							</span>
							<span class="dictionary__value">
								картой при получении
							</span>
						</li>
					</ul>
				</div>
				<div class="cart__block">
					<OrderItem :products="products"/>
					<div class="cart__total">
						<p>Доставка: <b>500 ₽</b></p>
						<p>Итого: <b>{{ products.length }}</b> товара на сумму <span>{{ orderInfo.totalPrice | numberFormat }} ₽</span></p>
					</div>
				</div>
				
			</form>
		</section>
	</main>
</template>
<script>
	import { mapGetters } from 'vuex';
	import OrderItem from '@/components/OrderItem'
	import numberFormat from "@/helpers/numberFormat";


	export default {
		data() {
			return {
				orderNotFoundLocal: this.$store.state.orderNotFound,
			};
		},
		filters: {numberFormat},		
		components: { OrderItem },
		computed: {
			...mapGetters({orderInfo:'orderInfo', products:'orderProducts'}),
		},
		created() {
			if (this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id ) {
				return;
			}
			this.$store.dispatch('loadOrderInfo', this.$route.params.id);
		},
		watch: {
			orderNotFoundLocal() {
				console.log(this.OrderNotFoundLocal);
				this.$router.push({name: 'notFound'});
		},
	},
	}
</script>