<template>
	<div class="form__counter">
		<button type="button" @click="decreaseProduct" aria-label="Убрать один товар">
			<svg width="12" height="12" fill="currentColor">
				<use xlink:href="#icon-minus"></use>
			</svg>
		</button>
		
		<input type="text" v-model="localProductAmount">
		
		<button type="button" @click="increaseProduct" aria-label="Добавить один товар">
			<svg width="12" height="12" fill="currentColor">
				<use xlink:href="#icon-plus"></use>
			</svg>
		</button>
	</div>

</template>
<script>
export default {
	props: ['productAmount'],
	data() {
		return  {
			localProductAmount: this.productAmount
		};
	},
	watch: {
		localProductAmount(value) {
			this.$emit('update:productAmount', value);
		},
	},
	methods: {
		increaseProduct() {
			this.localProductAmount++;
			this.$emit('update:productAmount', this.localProductAmount);
		},
		decreaseProduct() {
			if (this.productAmount > 1) {
				this.localProductAmount--;
				this.$emit('update:productAmount', this.localProductAmount);
			}
		}
	}
}
</script>