<template>
	<main class="content container">
		<div class="content__top">
      <ul class="breadcrumbs">
		<li class="breadcrumbs__item">
		<router-link class="breadcrumbs__link" :to="{name: 'main'}">
			Каталог
		</router-link>
		</li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <span class="content__info">
        {{ AmountProducts }} товара
      </span>
    </div>
	<section class="cart" v-show="$store.state.cartLoading">загружаем</section>		
	<section class="cart" v-show="!$store.state.cartLoading">
		<form class="cart__form form" action="#" method="POST">
			<div class="cart__field">
			<ul class="cart__list">
				<CartItem v-for="item in products" :item="item" :key="item.productId"/>
			</ul>
			</div>

			<div class="cart__block">
			<p class="cart__desc">
				Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
			</p>
			<p class="cart__price">
				Итого: <span>{{ totalPice | numberFormat }} ₽</span>
			</p>

			<router-link tag="button" v-show="AmountProducts > 0" :to="{name: 'order'}" class="cart__button button button--primery" type="submit">
				Оформить заказ
			</router-link>
			</div>
		</form>
	</section>
</main>
</template>
<script>
	import numberFormat from "@/helpers/numberFormat";
	import { mapGetters } from 'vuex';
	import CartItem from '@/components/CartItem';

	export default {
		components: { CartItem },
		filters: {numberFormat},
		computed: {
			...mapGetters({products:'cardDetailProducts', totalPice: 'cardTotalPirce', AmountProducts: 'cardAmountProducts'}),
		},
	}
</script>