import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
//import router from "@/router/index.js";	// redirect to 404 page
import { API_BASE_URL } from "../config.js";

Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		cartProducts: [],
		userAccessKey: null,
		cartProductsData: [],
		cartLoading: false,
		orderInfoLoading: false,
		orderNotFound: false,
		orderInfo: null,
		orderProducts: null,
	},
	mutations: {
		updateCartProductAmount(state, { productId, amount }) {
			const item = state.cartProducts.find(
				(item) => item.productId === productId
			);
			if (item) {
				item.amount = amount;
			}
		},
		updateUserAccessKey(state, accessKey) {
			state.userAccessKey = accessKey;
		},
		updateCartProductsData(state, items) {
			state.cartProductsData = items;
		},
		syncCartProducts(state) {
			return (state.cartProducts = state.cartProductsData.map((item) => {
				return {
					productId: item.product.id,
					amount: item.quantity,
				};
			}));
		},
		changeStatusLoading(state, value) {
			state.cartLoading = value
		},
		changeOrderInfoLoading(state, value) {
			state.orderInfoLoading = value
		},
		changeOrderNotFound(state, value) {
			console.log(value);
			state.orderNotFound = value
		},
		resetCart(state) {
			state.cartProducts = [];
			state.cartProductsData = [];
		},
		updateOrderInfo(state, orderInfo) {
			state.orderInfo = orderInfo;
		},
	},
	getters: {
		cardDetailProducts(state) {
			return state.cartProducts.map((item) => {
				const product = state.cartProductsData.find(
					(p) => p.product.id === item.productId
				).product;
				return {
					...item,
					product: { ...product, image: product.image.file.url },
				};
			});
		},
		cartProducts(state) {
			return state.cartProductsData.map((item) => {
				return {
					title:item.product.title,
					id:item.product.id,
					price: item.price * item.quantity,
				}
			} )
		},
		cardTotalPirce(state, getters) {
			return getters.cardDetailProducts.reduce(
				(acc, item) => item.product.price * item.amount + acc,
				0
			);
		},
		cardAmountProducts(state, getters) {
			return getters.cardDetailProducts.length;
		},
		orderInfo(state) {
			return state.orderInfo;
		},
		orderProducts(state) {
			return state.orderInfo.basket.items.map((item) => {
				return {
					title:item.product.title,
					id:item.product.id,
					price: item.price * item.quantity,
				}
			} )
		},
	},
	actions: {
		loadCart(context) {
			context.commit('changeStatusLoading',true);
			return (new Promise(resolve => setTimeout(resolve,500)))
			.then(() =>{
				return axios
					.get(API_BASE_URL + "/baskets", {
						params: {
							userAccessKey: context.state.userAccessKey,
						},
					})
					.then((response) => {
						if (!context.state.userAccessKey) {
							localStorage.setItem(
								"userAccessKey",
								response.data.user.accessKey
							);
							context.commit("updateUserAccessKey",response.data.user.accessKey);
						}
						context.commit(
							"updateCartProductsData",
							response.data.items
						);
						context.commit("syncCartProducts");
						context.commit('changeStatusLoading',false);

					});
			})
		},
		addProductToCart(context, { productId, amount }) {
			return (new Promise(resolve => setTimeout(resolve,2000)))
				.then(()=>{
				return axios
				.post(
					API_BASE_URL + "/baskets/products",
					{
						productId: productId,
						quantity: amount,
					},
					{
						params: {
							userAccessKey: context.state.userAccessKey,
						},
					}
				)

				.then((response) => {
					context.commit("updateCartProductsData",response.data.items);
					context.commit("syncCartProducts");
				});
				})
			
		},
		updateCartProductAmount(context, { productId, amount }) {
			context.commit('updateCartProductAmount', { productId, amount });

			return axios
			.put(
				API_BASE_URL + "/baskets/products",
				{
					productId: productId,
					quantity: amount,
				},
				{
					params: {
						userAccessKey: context.state.userAccessKey,
					},
				}
			)

			.then((response) => {
				context.commit("updateCartProductsData",response.data.items);
			})
			.catch(() => {
				context.commit("syncCartProducts");
			})
		},
		deleteCartProduct(context, productId ) {
			return axios
			.delete (API_BASE_URL + '/baskets/products?userAccessKey=' + context.state.userAccessKey, {
                data: {
                    productId: productId
                }
            })
			.then((response) => {
				context.commit("updateCartProductsData",response.data.items);
				context.commit("syncCartProducts");
			})
			.catch(() => {
				context.commit("syncCartProducts");
			})
		},
		loadOrderInfo(context, orderId) {
			context.commit('changeOrderInfoLoading',true);
			context.commit('changeOrderNotFound',false);
			return (new Promise(resolve => setTimeout(resolve,500)))
			.then(()=>{
				return axios
				.get(API_BASE_URL + "/orders/" + orderId, {
					params: {
						userAccessKey: context.state.userAccessKey,
					},
				})
				.then((response) => {
					context.commit('updateOrderInfo',response.data);
					context.commit('changeOrderInfoLoading',false);
				})
				.catch((response)=>{
					console.log(response.response.data.error.message);
					context.commit('changeOrderInfoLoading',false);
					context.commit('changeOrderNotFound',true);
					//router.push({name: 'notFound'}); // redirect to 404 page
			})
		})
		},
	},
});
