<template>
	<ul class="colors colors--black">
		<li class="colors__item" v-for="color in colors" :key="color.id">
			<label class="colors__label">
				<input class="colors__radio sr-only" type="radio" name="color" :value="color.id" v-model="colorId" @change="$emit('getColors', Number($event.target.value))">
				<span class="colors__value" :style="{backgroundColor: color.code}"></span>
			</label>
		</li>
	</ul>
</template>
<style>
	.colors__value:hover {
		--border-color: #FF6B00;
	}
	.colors-radio:checked {
		--border-color: rgb(18, 19, 18);
	}
	.colors-radio {
		--border-color: #ff0062;
	}
</style>
<script>

import axios from 'axios';
import {API_BASE_URL} from './../config.js';

export default {
	data() {
			return {
				colorId: 0,
				colorsData: null
			}
		},

	props:{
		productColorId: {
			type: Number,
			default: 1
		}},
		computed: {
			colors() {
				return this.colorsData ? this.colorsData.items : [];
			},
		},
		methods: {
			loadColors() {
				return axios
				.get(API_BASE_URL + '/colors')
			.then(response => this.colorsData = response.data)
			},			
		},
		created() {
			this.loadColors();
		}
}
</script>